  flex-direction: column;
  align-items: center;
}

.Badges__container {
  width: 100%;
  max-width: 580px;
  margin: 0 auto;
  padding: 0 1rem;
}

.Badges_conf-logo {
  margin-bottom: 2rem;
}

.Badges__buttons {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
}

.fn_ln_list,
.img_twttr_list,
.job_title_list {
  margin-bottom: 0px;
}

.fn_ln_list {
  font-weight: bold;
}

.img_twttr_list {
  color: #2caae1;
}

.job_title_list {
  font-size: 14px;
}

.cont_badges_list {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3);
}

.bages_list_avtr_url {
